body{
  background-color: #7961e6ef;
}
.App{
  background-color: #15033d94;
  min-height:500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: rgb(9, 50, 63);
  border: 0; 
  color: #f7f0f0;
  width: 200px;
  height: 60px;
  margin: 20px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 10px;
}
#to-do-form input::placeholder{
  color: rgba(248, 242, 242, 0.87);
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 3px solid rgb(8, 61, 22);
  background-color:rgb(103, 165, 111); 
  cursor: pointer;
}
#to-do-form button:focus{
  outline: none;
}